<template>
  <div class="table-calender container-fluid">
    <ul class="nav nav-tabs" role="tablist">
      <li
        v-for="(salon, index) in avaTables.salons"
        :key="index"
        class="nav-item"
      >
        <a
          :class="['nav-link', { active: activeSalonIndex === index }]"
          :href="'#tab-' + index"
          role="tab"
          @click="changeActiveSalon(index)"
        >
          {{ salon.name }}
        </a>
      </li>
    </ul>

    <div class="tab-content">
      <div
        v-for="(salon, index) in avaTables.salons"
        :key="index"
        :id="'tab-' + index"
        class="tab-pane"
        :class="{
          show: activeSalonIndex === index,
          'd-none': activeSalonIndex !== index,
        }"
        role="tabpanel"
      >
        <div class="row mt-5">
          <div class="col-md-12">
            <table class="table table-dark table-hover">
              <thead>
                <tr>
                  <th v-for="(hour, i) in avaTables.hours" :key="i">
                    {{ hour }}
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="table in salon.tables" :key="table.id">
                  <td
                    v-for="(available, index) in table.availability"
                    :key="index"
                  >
                    <div
                      class="reservations-wrap"
                      v-if="available"
                      @click="getTableId(table.id)"
                    >
                      <div
                        class="status rounded-3"
                        :class="{
                          'bg-success': available,
                          'bg-secondary': !available,
                        }"
                        :data-id="table.id"
                      >
                        <span @click="printAvailableHours(table.id)">
                          طاولة {{ table.name }}
                        </span>
                        <input type="hidden" :value="table.id" />
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      avaTables: {
        hours: [],
        salons: [],
      },
      activeSalonIndex: 0, // تحديد الصالة الافتراضية التي يجب عرض طاولاتها
    };
  },
  methods: {
    getTableId(tableId) {
      console.log(tableId);
    },
    async fetchData() {
      try {
        const response = await axios.get("https://vkoa.net/api/time-slots");
        this.avaTables.hours = response.data.hours;
        this.avaTables.salons = response.data.salons;
        console.log("success");
      } catch (error) {
        console.error("Failed to fetch data:", error);
      }
    },
    printAvailableHours(tableId) {
      const salon = this.avaTables.salons[this.activeSalonIndex];
      const table = salon.tables.find((t) => t.id === tableId);
      if (table) {
        const availableHours = this.avaTables.hours
          .filter((hour, index) => table.availability[index])
          .map((hour) => hour);

        console.log(
          "Available hours for Table " + table.name + " in " + salon.name + ":",
          availableHours
        );
      }
    },
    changeActiveSalon(index) {
      this.activeSalonIndex = index;
    },
  },
  mounted() {
    this.fetchData();
  },
};
</script>
