  border: 1px solid #646464 !important;
  text-align: center !important;
}
.nav-tabs a {
  color: #fff;
}
.status {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 65px;
}
.fade {
  display: none !important;
}
.show {
  display: block !important;
}
</style>
